.new-expense_controls {
    display: flex;
    flex-wrap: wrap;
    gap: 1rem;
    margin-bottom: 1rem;
    text-align: left;
  }
  
  .new-expense_control label {
    font-weight: bold;
    margin-bottom: 0.5rem;
    display: block;
  }
  
  .new-expense_control input {
    font: inherit;
    padding: 0.5rem;
    border-radius: 6px;
    border: 1px solid #ccc;
    width: 20rem;
    max-width: 100%;
  }
  
  .new-expense_actions {
    text-align: right;
  }
  @media(max-width: 850px){
    .new-expense_control{
      display: flex;
      align-items: center;
      justify-content: center;
      flex-direction: column;
    }
    .new-expense_control input {
     display: flex;
     align-self: center;
      align-content: center;
    justify-self: center;
      width: 20rem;
      max-width: 50%;
    }
    .new-expense_controls {
      align-items: center;
      justify-content: center;
      
      
    }
    .new-expense_actions button{
      margin-bottom: 1rem;
    }

  }
 